/* stylelint-disable declaration-no-important */

.arve-license-input {
	width: 280px;
}

.arve-btn {
	padding-left: 3px;
}

.arve-icon::before {
	content: "\f236" !important; /* dashicon-video-alt */
}

.edit-shortcode-form-fields.shortcode-ui-edit-arve .field-block {
	padding-top: .4em;
	padding-bottom: .4em;

	label {
		display: inline-block;
		margin: 0;

		&:first-child {
			width: 110px;
		}
	}
}

.edit-shortcode-form-fields.shortcode-ui-edit-arve {

	.shortcode-ui-attribute-upload_date input,
	.shortcode-ui-attribute-aspect_ratio input,
	.shortcode-ui-attribute-url input,
	.shortcode-ui-attribute-title input,
	.shortcode-ui-attribute-description input {
		width: 700px;
	}
}

.arve-list-small {
	padding-left: 20px;
	font-size: .85em;
	list-style: square;
}

.arve-list-small li {
	margin-bottom: 2px;
}

.arve-settings-page-ad {
	padding-right: .7rem !important;
	margin: 0;
}

.arve-corner-spacer {
	float: right;
	width: 20px;
	height: 38px;
}

@media all and (min-width: 1400px) {

	.arve-settings-page-ad {
		position: absolute !important;
		right: 0;
		width: 320px;
	}

	.arve-options-wrap {
		position: relative;
		padding-right: 350px;
	}
}

#arve-shortcode {
	margin-top: 1em;
	font-size: 1.2em;
	line-height: 1.2;
}
